import _ from 'lodash';
import * as Actions from '../actions';

const initialState = {
    apiCallsInProgress: 0
};

const apiReducer = (state = initialState, action) => {
    if (action.type === Actions.API_CALL_BEGIN) {
        return {
            ...state, apiCallsInProgress: 1 // state.apiCallsInProgress + 1
        };

    } else if (isApiCallFinished(action.type) || action.type === Actions.API_CALL_ERROR) {
        return {
            ...state, apiCallsInProgress: 0 // state.apiCallsInProgress - 1
        };
    }

    return state;
};

function isApiCallFinished(type) {
    return _.endsWith(type, '_RESP');
}

export default apiReducer;