import axios from 'axios/index';

export const GET_MOVIE_DETAILS_RESP = 'GET MOVIE DETAILS_RESP';
export const GET_CASTS_RESP = 'GET CASTS_RESP';
export const GET_MOVIES_RESP = 'GET MovieS_RESP';
export const GET_CAST_INFO_RESP = 'GET CAST INFO_RESP';
export const GET_CAST_INFO_MOVIE_RESP = 'GET CAST INFO MOVIE_RESP';

export const API_CALL_BEGIN = 'API_CALL_BEGIN';
export const API_CALL_ERROR = 'API_CALL_ERROR';

export const ApiCallBeginAction = () => ({
    type: API_CALL_BEGIN
});

export const ApiCallErrorAction = () => ({
    type: API_CALL_ERROR
});

export function getMovies(routeParams) {
    const request = axios.get('//api.themoviedb.org/3/movie/popular?api_key=5d3c7870ac77bed5ee45c3b44470eca6&language=en-US&page=1', {
        params: routeParams
    });

    return (dispatch) =>
        request.then((response) =>
            dispatch({
                type: GET_MOVIES_RESP,
                payload: response.data.results,
                routeParams
            })
        );
}

export function getMovieDetails(routeParams) {

    const movieId = routeParams.id;
    const URL = `//api.themoviedb.org/3/movie/${movieId}?api_key=5d3c7870ac77bed5ee45c3b44470eca6&language=en-US`;

    const request = axios.get(URL, {});

    return (dispatch) => {

        dispatch(ApiCallBeginAction());

        return request.then((response) =>
            dispatch({
                type: GET_MOVIE_DETAILS_RESP,
                payload: response.data,
                routeParams
            })
        );
    }

}


