import * as Actions from '../actions';
import _ from '@lodash';

const initialState = {
    entities: [],
    searchText: '',
    selectedMovieIds: [],
    routeParams: {},
    movies: [],
    casts: [],
    castInfo: {},
    castMovieInfo: []
};

const MoviesReducer = function (state = initialState, action) {
    switch (action.type) {
        case Actions.GET_MOVIES_RESP:
            {
                return {
                    ...state,
                    entities: action.payload,
                    routeParams: action.routeParams
                };
            }
        case Actions.GET_MOVIE_DETAILS_RESP:
            {
                return {
                    ...state,
                    movies: action.payload,
                    routeParams: action.routeParams
                };
            }
        case Actions.GET_CASTS_RESP:
            {
                return {
                    ...state,
                    casts: action.payload,
                    routeParams: action.routeParams
                };
            }
        case Actions.GET_CAST_INFO_RESP:
            {
                return {
                    ...state,
                    castInfo: action.payload,
                    routeParams: action.routeParams
                };
            }
        case Actions.GET_CAST_INFO_MOVIE_RESP:
            {
                return {
                    ...state,
                    castMovieInfo: action.payload,
                    routeParams: action.routeParams
                };
            }

        default:
            {
                return state;
            }
    }
};

export default MoviesReducer;
